.form-container {
  position: relative; }

.editor-block {
  position: absolute; }
  .editor-block.editor-block-focus::after {
    content: '';
    position: absolute;
    top: -3px;
    bottom: -3px;
    left: -3px;
    right: -3px;
    border: dashed 1px #409eff; }

.number-range {
  display: inline-flex;
  width: 225px;
  min-height: 40px;
  align-items: stretch; }
  .number-range > div {
    flex: 1; }
    .number-range > div input {
      width: 100%;
      height: 100%;
      margin: 0;
      padding: 0;
      box-sizing: border-box; }
  .number-range span {
    margin: 0 8px;
    display: inline-flex;
    align-items: center; }
